.App {
  text-align: center;
}
.m-top-20{
    margin-top: 20px !important;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.box-container {
    margin-bottom: 10px;
}
.box-container.events {
    min-width: 96%;
}

.box{
    height: 100%;
    background: #e2e2e2;
    padding: 10px 5px;
}

.row-eq-height {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display:         flex;
}

.sort-by p {
    cursor: pointer;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.selected{
    border: 1px solid #61dafb;
}

.hidden{
    visibility: hidden;
}

.sort-by button {
    padding: 4px;
    margin-right: 10px;
    font-size: 15px;
}
